import {
  React,
  connect,
  reduxForm,
  ReanderField,
  Field,
  ReanderSelect,
  getData2,
  Gramasi,
} from "../../../components/helpers/library";
import { GetDataGroup, getDataJenis } from "../TambahBarang/Module";

const maptostate = (state) => {
  return {
    DataGroup: state.datamaster.GetDataGroup,
    DataJenis: state.datamaster.getDataJenis,
  };
};
class HeadPenerimaanBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      isChecked: true,
    };
  }
  chnageValue(e) {
    this.setState({
      isChecked: !this.state.isChecked,
    });
    if (this.state.isChecked === true) {
      getData2("penerimaan-suplier/trx-number").then((res)=>{
          this.props.change('no_pengiriman',res.data.no_terima)
      })
    }else{
        this.props.change('no_pengiriman','')
    }
  }

  componentDidMount() {
    this.props.dispatch(GetDataGroup());
    this.props.dispatch(getDataJenis());
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <div className="row">
          <div className="col-3">
            <Field
              label="No Pengiriman"
              name="no_pengiriman"
              readOnly
              placeholder={"Silahkan No Pengiriman"}
              component={ReanderField}
            />
          </div>
          <div className="col-3">
            <div className="form-group">
              <label>&nbsp;</label>
              <br />
              <label>
                <Field
                  id="bukan_barang_supliar"
                  name="bukan_barang_supliar"
                  component="input"
                  onClick={(e) => this.chnageValue(e)}
                  type="checkbox"
                  value={false}
                />{" "}
                Bukan Suplier
              </label>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-4">
            <Field
              label="Kategori"
              name="kode_group"
              placeholder={"Silahkan Pilih kategori"}
              onChange={(e) =>
                this.setState({
                  kode_group: e,
                })
              }
              options={this.props.DataGroup.map((data_group) => {
                let data = {
                  value: data_group.kode_group,
                  name: data_group.kode_group,
                };
                return data || [];
              })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-3">
            <Field
              label="jenis"
              name="jenis"
              placeholder={"Silahkan Pilih Jenis"}
              options={this.props.DataJenis
                .filter((list) => list.kode_group === this.state.kode_group)
                .map((list) => {
                  let data = {
                    value: list.kode_dept,
                    name: list.kode_dept 
                  };
                  return data;
                })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-3">
            <Field
              label="Berat"
              name="berat"
              normalize={Gramasi}
              readOnly={this.state.isChecked}
              placeholder={"Silahkan Pilih Berat"}
              component={ReanderField}
            />
          </div>
          <div className="col-2">
            <button className="btn btn-primary btn-block mt-4">
              {" "}
              <i className="fa fa-plus"></i>{" "}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

HeadPenerimaanBarang = reduxForm({
  form: "HeadPenerimaanBarang",
  enableReinitialize: true,
})(HeadPenerimaanBarang);
export default connect(maptostate)(HeadPenerimaanBarang);
